import React, { useState, useEffect } from "react";
import "./App.css";
import { makeStyles, useTheme } from "@mui/material/styles";
import { useForm } from "react-hook-form";
import {
  TextField,
  CardContent,
  Card,
  CardActions,
  Button,
  Grid,
  Divider,
  FormControl,
  FormLabel 
} from "@mui/material/";

const App = () => {

  const { register, handleSubmit, formState: { errors } } = useForm();
  const onSubmit = data => console.log(data);
  const [show, setShow] = useState(false);  

  const Myclick=()=>{
    setShow(true);
  }

  return (
    <div className="container">
      <Card className="card">

        <CardContent className="cardContent">

         { show ? (<> <div className="cardContainer">
            <FormControl component="fieldset" variant="standard">
            <FormLabel component="legend" className="label">LOGIN</FormLabel>
              <TextField
                id="user_id"
                label="User Id"
                variant="standard"
                placeholder="Enter here"
              />
              <br />
              <TextField
                id="password"
                label="Password"
                type="password"
                autoComplete="current-password"
                variant="standard"
                placeholder="Enter here"
              />
              <br />
            </FormControl>
            <div className="alignCenter">
              <Button variant="contained" size="small">Log in</Button>
            </div>
          </div>
          <Divider variant="middle" flexItem>OR</Divider>
          <form onSubmit={handleSubmit(onSubmit)}>
          <div className="cardContainerReg">
          <FormControl component="fieldset" variant="standard">
            <FormLabel component="legend" className="label">REGISTER</FormLabel>
            <TextField
              id="email"
              label="Email"
              variant="standard"
              placeholder="Enter here"
              {...register("email", {required:"Required Field", pattern: {
                value: /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i,
                message: "Invalid email id"
              }})}
              error={!!errors?.email}
              helperText={errors?.email ? errors.email.message: null}
            />
            <br />
            <TextField
              id="user_name"
              label="User Name"
              variant="standard"
              placeholder="Enter here"
              {...register("User_Name", {required:"Required Field" })}
              error={!!errors?.User_Name}
              helperText={errors?.User_Name ? errors.User_Name.message: null}
            />
            <br />
            <TextField
              id="password_reg"
              label="Password"
              type="password"
              autoComplete="current-password"
              variant="standard"
              name="password"
              placeholder="Enter here"
              {...register("password", {required:"Required Field", minLength: {
                value: 6,
                message: "Password must be more than 6 characters"
              }})}
              error={!!errors?.password}
              helperText={errors?.password ? errors.password.message: null}
            />
            <br />
            </FormControl>
            <div className="alignCenter">
              <Button type="submit" variant="contained" size="small">Sign up</Button>
            </div>
          </div>
          </form></>):<div><h1>Welcome to Homepage</h1><div className="alignCenter">
              <Button onClick={Myclick} variant="outlined" size="large">Log in Now</Button>
            </div></div>}
        </CardContent>
      </Card>
    </div>

  );
}

export default App;
